window.dom = {
  //åˆ›å»ºèŠ‚ç‚¹
  create(string) {
    const container = document.createElement("template");
    //trimåŽ»æŽ‰ä¸¤å¤´çš„ç©ºæ ¼
    container.innerHTML = string.trim();
    return container.content.firstChild;
  },

  //åœ¨å½“å‰èŠ‚ç‚¹åŽé¢åˆ›å»ºèŠ‚ç‚¹
  after(node, node2) {
    console.log(node.nextSibling);
    //æ‰¾åˆ°èŠ‚ç‚¹çš„çˆ¸çˆ¸ï¼Œè°ƒç”¨çˆ¸çˆ¸çš„æ–¹æ³•ï¼ŒæŠŠæ–°èŠ‚ç‚¹æ’å…¥ä¸‹ä¸€ä¸ªèŠ‚ç‚¹çš„å‰é¢
    node.parentNode.insertBefore(node2, node.nextSibling);
  },
  //åœ¨å½“å‰èŠ‚ç‚¹å‰é¢åˆ›å»ºèŠ‚ç‚¹
  before(node, node2) {
    node.parentNode.insertBefore(node2, node);
  },
  append(parent,node){
      parent.appendChild(node)
  },
  //æ–°å¢žçˆ¶èŠ‚ç‚¹
  wrap(node,parent){
      //å…ˆæ‰¾åˆ°è¿™ä¸ªèŠ‚ç‚¹ï¼ŒæŠŠæ–°èŠ‚ç‚¹æ·»åŠ ä¸ºå®ƒçš„å…„èŠ‚ç‚¹
      dom.before(node,parent)
      //æŠŠè¿™ä¸ªèŠ‚ç‚¹åˆ é™¤ï¼Œæ·»åŠ ä¸ºæ–°èŠ‚ç‚¹çš„å­èŠ‚ç‚¹
      dom.append(parent,node)
  },
  remove(node){
      node.parentNode.removeChild(node)
      return node
  },
  empty(node){
      const array = []
      let x = node.firstChild

      while(x){

          array.push(dom.remove(node.firstChild))
          x = node.firstChild
      }
      return array
  },
  attr(node,name,value){
      //å‡½æ•°é‡è½½
      if(arguments.length === 3){
          node.setAttribute(name, value)
      }else if(arguments.length ===2){
          return node.getAttribute(name)
      }
  },
  text(node,string){
      //é€‚é…
      if(arguments.length ===2){
          if ('innerText' in node) {
              node.innerHTML = string           //ie
          } else {
              node.textContent = string         //firefox/chrome
          }
      }else if(arguments.length ===1){
          if ('innerText' in node) {
              return node.innerHTML = string           //ie
          } else {
              return node.textContent = string         //firefox/chrome
          }
      }
    
      
  },
  html(node,string){
    if(arguments.length ===2){
        node.innerHTML = string
    }else if(arguments.length ===1){
        return node.innerHTML
    }
  },
    style(node,name,value){
      if (arguments.length === 3){
          console.log('çŽ°åœ¨ä¼ è¿›æ¥çš„å‚æ•°æœ‰3ä¸ª')
          //dom.style('div','color','red')
          node.style[name] = value
      }else if(arguments.length ===2){
          console.log('çŽ°åœ¨ä¼ è¿›æ¥çš„å‚æ•°æœ‰2ä¸ª')
          if(typeof name === 'string'){
              console.log('nameæ˜¯å­—ç¬¦ä¸²')
              //dom.style(div,'color')
              return node.style[name]
          }else if(name instanceof Object){
              console.log('nameæ˜¯å¯¹è±¡')
              //dom.style(div,{color:'red'})
              const object = name
              for (let key in object){
                  node.style[key] = object[key]
              }
          }
      }
    },
    class:{
      add(node,className){
          node.classList.add(className)
      },
        remove(node,className) {
          node.classList.remove(className)
        },
        has(node,className){
          return node.classList.contains(className)
        }
    },
    on(node,eventName,fn){
      node.addEventListener(eventName,fn)
    },
    off(node,eventName,fn){
      node.addEventListener(eventName,fn)
    },
    find(selector,scope){
      //å¦‚æžœæœ‰scopeï¼Œå°±åœ¨scopeé‡Œé¢è°ƒç”¨queryselectorAllï¼Œå¦åˆ™é»˜è®¤åœ¨documenté‡Œé¢è°ƒ
      return (scope||document).querySelectorAll(selector)
    },
    parent(node){
      return node.parentNode
    },
    children(node){
      return node.children
    },
    siblings(node){
      //åªè¦çœ‹åˆ°çš„è¿™ä¸ªå­èŠ‚ç‚¹ä¸æ˜¯ä¼ å…¥çš„ç»“ç‚¹ï¼Œå°±è¿”å›ž
      return Array.from(node.parentNode.children).filter(n=>n!==node)

    },
    next(node){
      let x = node.nextSibling
        while (x && x.nodeType === 3){
          x = x.nextSibling
        }
        return x
    },
    previous(node){
        let x = node.previousSibling
        while (x && x.nodeType === 3){
            x = x.previousSibling
        }
        return x
    },
    each(nodeList,fn){
        console.log('è¿™æ˜¯æˆ‘')
      for(let i=0;i<nodeList.length;i++){
          // console.log('å‡ºé”™äº†å—')
        fn.call(null,nodeList[i])
      }
    },
    index(node){
      const list =dom.children(node.parentNode)
        console.log('è¿›æ¥indexäº†')
        let i
        for(i=0;i<list.length;i++){
            if(list[i]===node){
                break
            }
        }
        return i
    }
};
